import ClubsSVG from '../../assets/Clover.svg';
import DiamondSVG from '../../assets/Diamond.svg';
import HeartSVG from '../../assets/Heart.svg';
import SpadeSVG from '../../assets/Spade.svg';
import { CardNumber, CardKind, CardKindColor } from '../../types';

export const errorMessages = {
  cardKind: 'Type parameter needs to be one of CardType enums.',
  cardNumber: 'Card number can not be more than 13',
};

export const getCardColor = (type: CardKind): CardKindColor => {
  if ([CardKind.CLUBS, CardKind.SPADES].includes(type)) return CardKindColor.BLACK;
  if ([CardKind.DIAMONDS, CardKind.HEARTS].includes(type)) return CardKindColor.RED;
  throw new Error(errorMessages.cardKind);
};

export const getCardText = (number: CardNumber): string => {
  switch (number) {
    case 1:
      return 'Ace';
    case 11:
      return 'Jack';
    case 12:
      return 'Queen';
    case 13:
      return 'King';
    default:
      if (number > 13) throw new Error(errorMessages.cardNumber);
      return number.toString();
  }
};

export const getCardSymbol = (type: CardKind): string => {
  switch (type) {
    case CardKind.CLUBS:
      return ClubsSVG;
    case CardKind.DIAMONDS:
      return DiamondSVG;
    case CardKind.HEARTS:
      return HeartSVG;
    case CardKind.SPADES:
      return SpadeSVG;
    default:
      throw new Error(errorMessages.cardKind);
  }
};
